<template>
  <v-app id="inspire" class="overflow-hidden" style="background: white !important; background-color: white !important;"> 
    <v-navigation-drawer v-model="drawer" dark app style="z-index: 204;">
      <v-list dense>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>
              <h1>Layers</h1>
              <v-list dense class="ml-3" >
                <v-switch v-model="sLeak" label="Leaks" @change="toggled" class="py-0 mb-0"></v-switch>
                <v-switch v-model="sStation" label="Stations" @change="toggled" class="py-0 my-0"></v-switch>
                <v-switch v-model="sRoute" label="Routes" @change="toggled" class="py-0 my-0"></v-switch>
                <v-switch v-model="sRegion" label="Regions" @change="toggled" class="py-0 my-0"></v-switch>
              </v-list>
            </v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider></v-divider>
        <v-expansion-panels multiple accordion v-model="expand"  class="ml-0 pl-0">
          <v-expansion-panel>
            <v-expansion-panel-header>Regions</v-expansion-panel-header>
            <v-divider></v-divider>
            <!-- Filter by region -->
            <v-expansion-panel-content>
              <v-list-item-content >
                <v-switch v-model="regions.cent" label="Central" class="py-0 my-0 mr-5"></v-switch>
                <v-switch v-model="regions.east" label="Eastern" class="py-0 my-0"></v-switch>
                <v-switch v-model="regions.rock" label="Rocky Mountain" class="py-0 my-0"></v-switch>
                <v-switch v-model="regions.rose" label="Wildrose" class="py-0 my-0"></v-switch>
                <!-- <v-autocomplete :items="communities" item-text="properties.name" label="Communities" outlined multiple
                   dense v-model="filterLeft.community"></v-autocomplete> </-->
              </v-list-item-content>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
        <!-- Emissions -->
        <!-- 
        <v-expansion-panels multiple accordion v-model="expand2"  class="ml-0 pl-0">
          <v-expansion-panel>
            <v-expansion-panel-header>Emissions</v-expansion-panel-header>
            <v-divider></v-divider>
            <v-expansion-panel-content>
              <v-list-item-content>
                <v-switch v-model="emissions.le" label="Leaks" @change="toggled2" class="py-0 my-0 mr-5"></v-switch>
                <v-switch v-model="emissions.re" label="Repaired" @change="toggled2" class="py-0 my-0 mr-5" :disabled="!emissions.le"></v-switch>
              </v-list-item-content>
            </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels> -->
      </v-list>
      <v-select :items="rounds" item-disabled="disabled" label="Rounds" v-model="sRounds" required class='px-2 pt-4' filled multiple dense> </v-select>
      <v-switch v-model="emissions.re" label="Repaired" @change="toggled2" class="px-10 my-0" :disabled="!emissions.le"></v-switch>
      <v-divider></v-divider>
      <v-col align="center">
        <v-btn color="red">reset</v-btn>
      </v-col>

    </v-navigation-drawer>
    <!-- Top Nav Bar -->
    <v-app-bar app color="Black" dark class="pl-2">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title class="font-weight-black">NEBA</v-toolbar-title>
      <v-spacer />
    </v-app-bar>
    <!-- CHART --> 
    <v-card flat style="position: absolute; bottom: 0; left: 0; z-index: 2;" width="600" height="350" dark class="myChart" >
       <canvas id="emissionChart"></canvas>
    </v-card>
    <!-- bottom bar -->
    <!-- <v-card flat style="position: absolute; bottom: 0; left: 35%; z-index: 2;" width="600" height="100" dark class="myChart px-10">
      <v-slider ticks="always" v-model="sRounds" :tick-labels="tickLabels" :max=2 step="1" tick-size="10"> </v-slider>
    </v-card> -->

    <!-- Leaflet Map --> 
    <v-main>
      <v-container class="fill-height pa-0 ma-0" fluid>
        <mymap />
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  import mymap from "./leaflet";
  import Chart from 'chart.js';
  import {eventBus} from '../main';
  //import axios from "axios";
  var chart;
  var ctx;

  export default {
    name: "toolbar",
    components: {
      mymap
    },
    props: {
      source: String
    },
    data: () => ({
      sheet: false,
      about: false,
      dialog: false,
      dialogTitle: "",
      sLeak: false,
      sRegion: true,
      sRoute: false,
      sStation: false,
      sRounds: ["1","2"],
      comment: '',
      menu: false,
      drawer: false,
      regions: {
        rose: true,
        cent: true,
        rock: true,
        east: true
      },
      rounds: [
        {text: "1", disabled: false}, 
        {text: "2", disabled: false},
        {text: "3", disabled: true}],
      hours: null,
      killo: null,
      expand: [],
      expand2: [],
      emissions: {
        le: true,
        re: false,
        dr: false
      },
      chartData: null,
      tickLabels: ["Round 1", "Round 2"],
      
    }),

    watch: {
      sRounds: function() {
        //sort if multiple selections
        //if (this.sRounds.length == 2) {
          //this.sRounds = thissort();
        //}

        //disable selection if its the only one remaining (prvent having nothing selected)
        if (this.sRounds.length == 1) {
          //reset everything to enabled - will need to change for round 3
          this.rounds[0].disabled = false;
          this.rounds[1].disabled = false;
          //diable the last one
          this.rounds[Number(this.sRounds[0])-1].disabled = true;
          //if something was unselected remove heatmap - change for round 3
          if (this.sRounds[0] == "1") {
              eventBus.$emit("toggle-heat-off2");
              eventBus.$emit("toggle-repair-off", ["2"]);
            } else {
              eventBus.$emit("toggle-heat-off");
              eventBus.$emit("toggle-repair-off", ["1"]);
          }
        }
        
        //adding selection
        if (this.sRounds.length >= 2) { //need to change for round 3
          this.rounds[0].disabled = false;
          this.rounds[1].disabled = false;
          //checks last index to see what to update
          if (this.sRounds[1] == "1" && this.emissions.re) {
            eventBus.$emit("toggle-repair-on", ["1"]);
          } else if (this.sRounds[1] == "1" && !this.emissions.re)  {
            eventBus.$emit("toggle-heat-on");
          }

          if (this.sRounds[1] == "2" && this.emissions.re) {
            eventBus.$emit("toggle-repair-on", ["2"]);
          } else if (this.sRounds[1] == "2" && !this.emissions.re) {
            eventBus.$emit("toggle-heat-on2");
          }
        }
      }
    },

    mounted() {
      eventBus.$on("make-chart", data => {
        console.log(data);
        ctx = document.getElementById('emissionChart');
        chart = new Chart(ctx, {
          type: 'horizontalBar',
          data: data,
          options: {
            legend: {
              labels: {
                fontColor: "white"
              }
            },
            scales: {
              xAxes: [{ stacked: true }],
              yAxes: [{ stacked: true }]
            }
          }
        });
        this.chart = chart;
      });

      
      eventBus.$on("pass-chart", data => {
        this.chartData = data;
      });
    },

    methods: {
      toggled() {
        //if leaks toggled off
        if (!this.sLeak) {
          eventBus.$emit("toggle-leak-off");
        } else if (this.sLeak) {
          eventBus.$emit("toggle-leak-on")
        }

        //if regions toggled off
        if (!this.sRegion) {
          eventBus.$emit("toggle-region-off");
        } else if (this.sRegion) {
          eventBus.$emit("toggle-region-on")
        }

        //if routes toggled off
        if (!this.sRoute) {
          eventBus.$emit("toggle-route-off");
        } else if (this.sRoute) {
          eventBus.$emit("toggle-route-on")
        }

        //if station toggled off
        if (!this.sStation) {
          eventBus.$emit("toggle-station-off");
        } else if (this.sStation) {
          eventBus.$emit("toggle-station-on")
        }

      },
      toggled2() {

        if (this.emissions.re) {
          if (this.sRounds.indexOf("1") >= 0) eventBus.$emit("toggle-heat-off"); 
          if (this.sRounds.indexOf("2") >= 0) eventBus.$emit("toggle-heat-off2");
          eventBus.$emit("toggle-repair-on", this.sRounds);
        } else if (!this.emissions.re) {

          if (this.sRounds.indexOf("1") >= 0) eventBus.$emit("toggle-heat-on");
          if (this.sRounds.indexOf("2") >= 0) eventBus.$emit("toggle-heat-on2");
          eventBus.$emit("toggle-repair-off", this.sRounds);
        }
      },
    },
  }

</script>


<style scoped>
 
  .myChart {
    opacity: 0.6;
  }

  .myChart:hover {
    opacity: 1;
  }

</style>