<template>
  <v-container class="fill-height" fluid id="map-container" position="static"></v-container>
</template>

<script>
import leaflet from "leaflet";
import { eventBus } from "../main.js";
import "leaflet.markercluster";
//import axios from "axios";
import "leaflet-contextmenu";
import "leaflet.heat";
//individual leak rates for every floc
import leakData from "./r1_cumulative_leak_info.json"
// all FLOC
import allLeaks from "./r1_all_emissions_novents.json"
// Regions 
import allregions from "./canadaregions.json"
// Routes information
import allroutes from "./r1_routes.json" //route shapes
import allroutes_leaks from "./r1_driving_emissions" //driving emissions information
import round2 from "./Round2_Aug28.json" //emissions from round 2
//console.log(allroutes_leaks);
var GeoJSON = require('geojson');

export default {
  name: "mymap",

  data() {
    return {
      leaf: null, // Interactive map
      cluster: false,
      path: null,
      stations: GeoJSON.parse(leakData, {Point: ['Lat', 'Long']}),
      emissions: GeoJSON.parse(allLeaks, {Point: ['Lat','Long']}),
      regions: allregions,
      routes: allroutes,
      clusterLeaks: null,
      clusterStations: null,
      colorRange: ['#3388ff','#45fc03','#e7fc03', '#fc8c03', '#fc0303'], //Green, Yellow, Orange, Red
      selected: false,
      emissionsHeat: [],
      driveHeat: [], //heatmap for emissions from driving 
      repaired: [], //heatmap for effective emissions
      r2: {
        data: GeoJSON.parse(round2, {Point: ['Lat','Long']}), //all data
        all: [], //heatmap for all leaks
        repaired: [] //heatmap for repaired leaks
      },
      chartData: 
        {
          all: 0,
          effective: 0,
          driving: 0,
          repaired: 0,
          backgroundColor: ['rgba(255, 99, 132, 0.6)','rgba(141, 247, 42,0.6)','rgba(255, 162, 0,0.6)','rgba(0, 128, 129, 0.2)'], 
          borderColor: ['rgba(255, 99, 132, 1)', 'rgba(141, 247, 42,1)','rgba(255, 162, 0,1)','rgba(0, 128, 129, 1)'],
          labels: ['Fugitive Emissions CO2e (10^6 Kg)', 'Driving Emissions CO2e (10^6 Kg)', 'Driving Emissions CO2e (Kg)']
        },
        chartData2:
        {
          all: 0,
          effective: 0,
          driving: 0,
          repaired: 0
        }
    };
  },

  mounted() {
  
    this.initMap();
    this.createLegend();

    eventBus.$on("toggle-leak-on", () => {
      this.leaf.addLayer(this.clusterLeaks);
    });

    eventBus.$on("toggle-leak-off", () => {
      this.leaf.removeLayer(this.clusterLeaks);
    });

    eventBus.$on("toggle-region-on", () => {
      this.leaf.addLayer(this.regions);
    });

    eventBus.$on("toggle-region-off", () => {
      this.leaf.removeLayer(this.regions);
    });

    eventBus.$on("toggle-route-on", () => {
      this.leaf.addLayer(this.routes);
    });

    eventBus.$on("toggle-route-off", () => {
      this.leaf.removeLayer(this.routes);
    });

    eventBus.$on("toggle-station-on", () => {
      this.leaf.addLayer(this.clusterStations);
    });

    eventBus.$on("toggle-station-off", () => {
      this.leaf.removeLayer(this.clusterStations);
    });
    // --------------- EMISSIONS ------------------
    eventBus.$on("toggle-heat-on", () => {
      this.leaf.addLayer(this.emissionsHeat);
    });

    eventBus.$on("toggle-heat-off", () => {
      this.leaf.removeLayer(this.emissionsHeat);
    });

    //turn on/off round 2 heatmaps 
    eventBus.$on("toggle-heat-on2", () => {
      this.leaf.addLayer(this.r2.all);
    });

    eventBus.$on("toggle-heat-off2", () => {
      this.leaf.removeLayer(this.r2.all);
    });

    /*
    eventBus.$on("toggle-drive-on", () => {
      this.leaf.addLayer(this.driveHeat);
    });

    eventBus.$on("toggle-drive-off", () => {
      this.leaf.removeLayer(this.driveHeat);
    }); */

    eventBus.$on("toggle-repair-on", selected => {
      for (let i = 0; i < selected.length; i++) {
        if (selected[i] == "1") {
          this.leaf.addLayer(this.repaired);
        }
        if (selected[i] == "2") {
          this.leaf.addLayer(this.r2.repaired);
        }
      }
    });

    eventBus.$on("toggle-repair-off", selected => {
      for (let i = 0; i < selected.length; i++) {
        if (selected[i] == "1") {
          this.leaf.removeLayer(this.repaired);
        }
        if (selected[i] == "2") {
          this.leaf.removeLayer(this.r2.repaired);
        }
      }
    });

  },

  methods: {
    
    //initializes leaflet map
    initMap() {
      var mapbox = new leaflet.tileLayer(
        //mapbox://styles/aa-variyun/ckbr7j1qg60gy1ioez5tftmj1
        //ckbr7sdd62h851jmn2g0zsfvf
        "https://api.mapbox.com/styles/v1/aa-variyun/ckbr7j1qg60gy1ioez5tftmj1/tiles/256/{z}/{x}/{y}?access_token={accessToken}",
        {
          attribution:
            'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
          maxZoom: 18,
          accessToken:
            "pk.eyJ1IjoiYWEtdmFyaXl1biIsImEiOiJjanZzYmhja2QxM2l5NGFvOHpqdXhiNDJvIn0.ez9bRvvx0eg9RZVmjiTPpQ"
        }
      );

      //satellite tile layer
      var satellite = new leaflet.tileLayer(
        "https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}",
        {
          attribution:
            'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
          maxZoom: 18,
          id: "mapbox.satellite",
          accessToken:
            "pk.eyJ1IjoiYWEtdmFyaXl1biIsImEiOiJjanZzYmhja2QxM2l5NGFvOHpqdXhiNDJvIn0.ez9bRvvx0eg9RZVmjiTPpQ"
        }
      );

      //leaflet map
      this.leaf = new leaflet.map("map-container", {
        center: [51.0839, -114.1439],
        zoom: 5,
        layers: [mapbox],
        contextmenu: true,
            contextmenuWidth: 170,
            contextmenuItems: [{
                text: 'Show Coordinates',
                callback: showCoordinates
            }, {
                text: 'Center Map Here',
                callback: centerMap
            }, {
                text: 'Zoom in',
                icon: 'https://img.icons8.com/metro/26/000000/zoom-in.png',
                callback: zoomIn
            }, {
                text: 'Zoom out',
                icon: 'https://img.icons8.com/metro/26/000000/zoom-out.png',
                callback: zoomOut
            }]
      });

      let _this = this;

      // context menu items
        function showCoordinates(e) {
            var msg = ''.concat(e.latlng.lat, ', ', e.latlng.lng);
            alert(msg);
        }

        function centerMap(e) {
            _this.leaf.panTo(e.latlng);
        }

        function zoomIn() {
            _this.leaf.zoomIn();
        }

        function zoomOut() {
            _this.leaf.zoomOut();
        }


      //adds scale bar
      //leaflet.control.scale().addTo(this.leaf);
      //adds layer control to the map
      var defaultTile = { Default: mapbox, Satellite: satellite };
      leaflet.control.layers(defaultTile).addTo(this.leaf);
     
      //add emissions to cluster
      this.clusterLeaks = leaflet.markerClusterGroup();
      this.clusterStations = leaflet.markerClusterGroup();
      //add all emissions to cluster
      this.emissions = leaflet.geoJson(this.emissions, {
        pointToLayer: function(feature, latlng) {
          return leaflet.marker(latlng)
        },
        onEachFeature: function(feature, layer) {
          layer.bindPopup(`Emission ID: ${feature.properties["Int.Emission ID"]}<br>
                            FLOC: ${feature.properties["FLOC"]}<br>
                            Station: ${feature.properties["Station Name"]}<br>
                            Leak Rate: ${feature.properties["Leak Rate"]}`);
        }
      });
      
      //add stations to cluster
      var stationMarker = leaflet.divIcon({
        className: "marker",
        iconSize: [32, 32]
      });

      // ------- ROUND 1 ----------
      this.stations = leaflet.geoJson(this.stations, {
        pointToLayer: function(feature, latlng) {
          //normalize values and add to heatmap
          //let intensity = (feature.properties["Impact"] - _this.limits[0])/ (_this.limits[1]-_this.limits[0]);
          //console.log(intensity);
          _this.chartData.repaired += feature.properties["Leak Rate Decreased"]
          if (feature.properties["Leak Rate"] != -1) {
            _this.emissionsHeat.push([latlng.lat, latlng.lng, feature.properties["Leak Rate"]]);
            _this.chartData.all += feature.properties["Leak Rate"];
            if (feature.properties["Leak Rate"] != 0) {
              _this.repaired.push([latlng.lat, latlng.lng, feature.properties["Effective Leak Rate"]]);
              _this.chartData.effective += feature.properties["Effective Leak Rate"];
            }   
          }
          return leaflet.marker(latlng, { icon: stationMarker })
        },
        onEachFeature: function(feature, layer) {
          let flocnum = "000000";
          flocnum = flocnum.substr(0, 6-String(feature.properties["FLOC"]).length) + String(feature.properties["FLOC"]);
          layer.bindPopup(`FLOC: ${flocnum}<br>
                            Effective Leak Rate (cfm): ${feature.properties["Effective Leak Rate"]}<br>
                            Associated Route: ${feature.properties["Associated Route"]}<br>
                            Environmental Impact: ${feature.properties["Impact"]}`);
        }
      });

      // --------- ROUND 2 --------------
      this.r2.data = leaflet.geoJson(this.r2.data, {
        pointToLayer: function(feature, latlng) {
          _this.r2.repaired.push([latlng.lat, latlng.lng, feature.properties["Effective Leak Rate"]]);
          _this.r2.all.push([latlng.lat, latlng.lng, feature.properties["Leak Rate"]]);
          _this.chartData2.all += feature.properties["Leak Rate"];
          _this.chartData2.repaired += feature.properties["Leak Rate Decreased"];
          _this.chartData2.effective += feature.properties["Effective Leak Rate"];
        }
      });
      // ---------- END ROUND 2 ----------

      //create new cluster layers
      this.clusterLeaks.addLayer(this.emissions);
      this.clusterStations.addLayer(this.stations);

      //add regions to map
      this.regions = leaflet.geoJson(this.regions, {
        onEachFeature: function(feature, layer) {
          layer.bindPopup(feature.properties.Name + " Region");
          layer.setStyle({fillOpacity: 0})
        }
      });

      //this.regions = leaflet.geoJson(this.regions);

      this.leaf.addLayer(this.regions);
      var iter = 0; 
      //add routes to map
      this.routes = leaflet.geoJson(this.routes, {
        onEachFeature: function(feature, layer) {
          var currentRoute = allroutes_leaks[iter]; 
          var route_color = '#3388ff'
          var im = parseFloat(currentRoute["Km per Site"]);
          if (im > 50 && im <= 100) {
            route_color = _this.colorRange[1];
          } 
          else if (im > 100 && im <= 250) {
            route_color = _this.colorRange[2];
          }
          else if (im > 250 && im <= 500) {
            route_color = _this.colorRange[3];
          }
          else if (im > 500) {
            route_color = _this.colorRange[4];
          }
          layer.setStyle({color: route_color});
          var popcontent = `Route: ${currentRoute["Route Name"]}
          <br>Total Distance: ${currentRoute["Total Distance"]} Km
          <br>Sites Visited: ${currentRoute["Sites Visited"]}
          <br>CO2e (Kg/Site): ${currentRoute["Emission per Site"]}
          <br>FLOCs: ${currentRoute["FLOCS"]}`;
          layer.bindPopup(popcontent);
          //highlight on hover
          layer.on('mouseover', function() {
            layer.setStyle({
              color: 'cyan',
              opacity: 1,
              weight: 5
            });
            layer.bringToFront();
          });
          
          layer.on('mouseout', function() {
            if (!_this.selected) {
              layer.setStyle({
                color: route_color,
                opacity: 1,
                weight: 3
              });
            }
          });
          iter += 1;
          //higlight on click
          layer.on('click', function() {
            _this.selected = !_this.selected;
          
            if (_this.selected) {
              layer.setStyle({
                color: 'cyan',
                opacity: 1,
                weight: 5
              });
              layer.bindTooltip(popcontent);
              layer.bringToFront();
            } else {
              layer.setStyle({
                color: route_color,
                opacity: 1,
                weight: 3
              });
              layer.closePopup();
              layer.unbindTooltip();
            }
          });
          _this.driveHeat.push([currentRoute["Lat"],currentRoute["Long"], currentRoute["Total Emissions"]/100000]);
          _this.chartData.driving += currentRoute["Total Emissions"]/100000; 
        } //eof oneachfeature
      });
      this.chartData2.driving = this.chartData.driving;
      // add heatmap
      // let options = {
      //   minOpacity: 0.08,
      //   max: 1005, 
      //   radius: 35,
      //   gradient: {0.05: 'blue', 0.1: 'lime', 0.2: 'orange', 1: 'red'},
      //   blur: 15
      // }
      let options2 = {
        minOpacity: 0.09,
        max: 3900, 
        radius: 30,
        gradient: {0.05: 'blue', 0.1: 'lime', 0.2: 'orange', 0.3: 'red'},
        blur: 15
      }
      //create heatmaps
      // ROUND 1 
      //this.driveHeat = leaflet.heatLayer(this.driveHeat, options2);
      this.emissionsHeat = leaflet.heatLayer(this.emissionsHeat, options2).addTo(this.leaf);
      this.repaired = leaflet.heatLayer(this.repaired, options2);
      // ROUND 2
      this.r2.all = leaflet.heatLayer(this.r2.all, options2).addTo(this.leaf); //here
      this.r2.repaired = leaflet.heatLayer(this.r2.repaired, options2);

      setTimeout(function() {
        eventBus.$emit("pass-chart", _this.chartData);
      },1000);

      //draw graph 
    
      setTimeout(function() {
        // ROUND 1 DATA
        let ton = 0.001;
        let all = _this.chartData.all * ton * 100000;
        let repaired = _this.chartData.repaired * ton * -100000; 
        let drive = _this.chartData.driving * 100000 * ton;
        let eff = _this.chartData.effective * ton * 100000;
        
        let all2 = _this.chartData2.all * ton * 100000;
        let repaired2 = _this.chartData2.repaired * ton * 100000; 
        let eff2 = _this.chartData2.effective * ton * 100000;

        let cdata = {
          labels: ["Total CO2e", "CO2e Reduced (Repairs)", "Current CO2e", "CO2e Driving"],
          datasets: [{
            label: "Round 1 (Tonnes)",
            data: [Math.round(all), Math.round(repaired), Math.round(eff), drive.toFixed(5)],
            backgroundColor: _this.chartData.backgroundColor,
            borderColor: _this.chartData.borderColor,
            minBarLength: 10
          },
          {
            label: "Round 2 (Tonnes)",
            data: [Math.round(all2), Math.round(repaired2), Math.round(eff2), drive.toFixed(5)],
            backgroundColor: ['rgba(255, 38, 38, 0.6)','rgba(38, 247, 165,0.6)','rgba(255, 222, 38,0.6)','rgba(255, 255, 255, 0.2)'],
            borderColor: _this.chartData.borderColor,
            minBarLength: 10
          }],
        };

        eventBus.$emit("make-chart", cdata);
      }, 1000);
    }, //---- end of map initialization ---

    createLegend() {
      let _this = this;
      //add legend to the map
      var legend = leaflet.control({position: 'bottomright'});

      legend.onAdd = function () {
        //let _this = this;
        var div = leaflet.DomUtil.create('div', 'info legend'),
        grades = [0, 50, 100, 250, 500],
        labels = ['Very Low', 'Low', 'Medium', 'High', 'Very High'];
        div.innerHTML += "<b>Environmental Impact</b><br>";
        // loop through our density intervals and generate a label with a colored square for each interval
        for (var i = 0; i < grades.length; i++) {
            div.innerHTML +=
               labels[i] + '<i style="background:' + _this.colorRange[i] + '"></i> ' +
                grades[i] + (grades[i + 1] ? '&ndash;' + grades[i + 1] + '<br>' : '+');
        }
        return div;
      };

      legend.addTo(this.leaf);
    }
  }
};

</script>

<style scoped>
@import "../../node_modules/leaflet.markercluster/dist/MarkerCluster.css";
@import "../../node_modules/leaflet.markercluster/dist/MarkerCluster.Default.css";

#map-container {
  z-index: 1;
}

</style>

<style>
.info.legend { 
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 5px;
  background-image: url("../assets/white.png");
}

.legend {
  line-height: 25px;
}

.legend i {
    width: 18px;
    height: 18px;
    float: left;
    margin-right: 8px;
    opacity: 0.7;
}


</style>
